/* Circular progress animation */
.outer-circle {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: conic-gradient(#00ffff calc(var(--percentage) * 1%),
      #e0e0e0 0);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.skill-icon {
  font-size: 30px;
  /* Make icons larger */
  display: flex;
  justify-content: center;
  /* Centers the icon horizontally */
  align-items: center;
  /* Centers the icon vertically */
  height: 100%;
}

/* Ensure three items per row on smaller screens (col-md-4) */
@media (max-width: 768px) {
  .col-md-4 {
    flex: 0 0 33.33%;
    max-width: 33.33%;
  }

  .skill-box {
    width: 80px;
    height: 80px;
  }
}

/* Ensure two items per row on larger screens */
@media (min-width: 769px) {
  .col-md-4 {
    flex: 0 0 25%;
    max-width: 25%;
  }
}

