/*==================== SERVICE CARD ====================*/
.card__container {
  display: flex;
  flex-wrap: wrap;
  gap: 60px;
  justify-content: center;
  width: 100%;
  max-width: 90%;
  margin: auto;
  padding: 60px 0;
}

.card__bx {
  --dark-color: #080d1c;
  --dark-alt-color: #777777;
  --white-color: #ffffff;
  --button-color: #333333;
  --transition: 0.5s ease-in-out;
  font-family: inherit;
  height: 500px;
  padding: 10px;
  position: relative;
  display: flex;
  justify-content: center;
  font-family: 'Times New Roman', Times, serif;
  align-items: center;
  background: var(--dark-color);
  transition: var(--transition);
}

.card__bx::before,
.card__bx::after {
  content: "";
  position: absolute;
  z-index: -1;
  transition: var(--transition);
}

.card__bx::before {
  inset: -10px 50px;
  border-top: 4px solid var(--clr);
  transform: skewY(15deg);
  border-bottom: 4px solid var(--clr);
}

.card__bx:hover::before {
  inset: -10px 40px;
  transform: skewY(0deg);
}

.card__bx::after {
  /* inset: 60px -10px; */
  border-left: 4px solid var(--clr);
  transform: skew(15deg);
  border-right: 4px solid var(--clr);
}

.card__bx:hover::after {
  inset: 40px -10px;
  transform: skew(0deg);
}

.card__bx .card__data {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 30px;
  text-align: center;
  padding: 0 20px;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.card__bx .card__data .card__icon {
  height: 80px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: var(--text-color);
  background-color: var(--dark-color);
  transition: var(--transition);
}

.card__bx .card__data .card__icon {
  color: var(--clr);
  box-shadow: 0 0 0 4px var(--dark-color), 0 0 0 6px var(--clr);
}

.card__bx:hover .card__data .card__icon {
  color: var(--dark-color);
  background-color: var(--clr);
  box-shadow: 0 0 0 4px var(--dark-color), 0 0 0 300px var(--clr);
}

.card__bx .card__data .card__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}

.card__bx .card__data h3 {
  font-size: 1.5rem;
  font-weight: 500;
  color: var(--white-color);
  transition: var(--transition);
}

.card__bx:hover .card__data h3 {
  color: var(--dark-color);
  transition: var(--transition);
}

.card__bx .card__data p {
  font-size: 0.9rem;
  color: var(--dark-alt-color);
  transition: var(--transition);
}

.card__bx:hover .card__data p {
  color: var(--dark-color);
  transition: var(--transition);
}

.card__bx .card__data a {
  position: relative;
  display: inline-flex;
  padding: 8px 15px;
  text-decoration: none;
  font-weight: 500;
  margin-top: 10px;
  border: 2px solid var(--clr);
  color: white;
  background: linear-gradient(40deg, #2096ff, #05ffa3);
  transition: var(--transition);
}

.card__bx:hover .card__data a {
  color: var(--clr);
  background-color: var(--dark-color);
}

.card__bx:hover .card__data a:hover {
  border-color: var(--dark-color);
  color: var(--dark-color);
  background-color: var(--clr);
}

/* ///Experience */

ul.timeline {
  position: relative;
  list-style-type: none;
  font-family: 'Times New Roman', Times, serif;
}

ul.timeline .event {
  transition: background-color 0.5s ease-in-out, color 0.5s ease-in-out;
}

ul.timeline .event:hover {
  background-color: rgba(0, 255, 255, 1);
  color: black;
}



ul.timeline .event {
  position: relative;
  padding: 16px;
  background: #080D1C;
  border-radius: 2px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .2), 0 1px 1px 0 rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
  padding: 16px;
  margin-bottom: 30px;
}


ul.timeline h3 {
  font-size: 1.5em;
  margin-top: 0;
  margin-bottom: 10px;
}

ul.timeline .time {
  position: absolute;
  display: block;
  width: 120px;
  top: 35px;
  left: -179px;
  font-size: 0.9em;
  text-align: right;
  font-weight: 600;
  text-transform: uppercase;
}

ul.timeline .time>.glyphicon-time {
  top: 2px;
}

ul.timeline .left-arrow:before {
  position: absolute;
  top: 30px;
  left: -15px;
  display: inline-block;
  border-top: 15px solid transparent;
  border-right: 15px solid #ddd;
  border-left: 0 solid #ddd;
  border-bottom: 15px solid transparent;
  content: ' ';
}

ul.timeline .left-arrow:after {
  position: absolute;
  top: 30px;
  left: -14px;
  display: inline-block;
  border-top: 15px solid transparent;
  border-right: 15px solid #fff;
  border-left: 0 solid #fff;
  border-bottom: 15px solid transparent;
  content: ' ';
}

